body {
    background: #E4EDDB;
    margin:0;
      padding:0;
    padding: 80px 0 0 0;
    font-family: 'Roboto', sans-serif;
    
  }
  *{
    margin:0;
    padding:0;
    -webkit-box-sizing:border-box;
            box-sizing:border-box
  }
  
  li{ list-style:none;}
  
  .noFill {
    fill: none;
  }
  
  .header {
    width: 100%; height: 80px;position: fixed;
    padding: 15px;
    top: 0;
    left: 0;
    z-index: 5;
  
    background: #1A3C40;
    -webkit-box-shadow: 0px 2px 4px rgba(44, 62, 80, 0.15);
            box-shadow: 0px 2px 4px rgba(44, 62, 80, 0.15);
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
  }
  .input{
    width: 100%;
    height: 50px;
    float: left;
    color: #FFF;
    font-size: 15px;
    font-weight: 400;
    text-indent: 18px;
    padding: 0 60px 0 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 5px 25px 25px 5px;
    border: 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
  
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  header .input {
    width: 100%;
    height: 50px;
    float: left;
    color: #FFF;
    font-size: 15px;
    font-weight: 400;
    text-indent: 18px;
    padding: 0 60px 0 0;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 5px 25px 25px 5px;
    border: 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
  
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  .button{
    width: 50px;
    height: 50px;
    position:absolute;
    top:15px;
    right:15px;
    z-index:2;
    border-radius: 25px;
    background: #fff;
    border: 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  header button {
    width: 50px;
    height: 50px;
  
    position:absolute;
    top:15px;
    right:15px;
    z-index:2;
  
    border-radius: 25px;
    background: #fff;
    border: 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    cursor: pointer;
  
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  .container {
    width: 100%;
    float: left;
    padding: 15px;
  }
  
  ul.todo {
    width: 100%;
    float: left;
  }
  
  ul.todo li {
    width: 100%;
    min-height: 50px;
    float: left;
    font-size: 14px;
    font-weight: 500;
    color: #444;
    line-height: 22px;
  
    background: #fff;
    border-radius: 5px;
    position: relative;
    -webkit-box-shadow: 0px 1px 2px rgba(44, 62, 80, 0.10);
            box-shadow: 0px 1px 2px rgba(44, 62, 80, 0.10);
    margin: 0 0 10px 0;
    padding: 14px 100px 14px 14px;
    word-break: break-word;
  }
  
  ul.todo li:last-of-type {
    margin: 0;
  }
  
  ul.todo li .buttons {
    width: 100px;
    height: 50px;
  
    position: absolute;
    top: 0;
    right: 0;
  }
  
  ul.todo li .buttons button {
    width: 50px;
    height: 50px;
    float: left;
    background: none;
    position: relative;
    border: 0px;
    -webkit-box-shadow: none;
            box-shadow: none;
    outline: none;
    cursor: pointer;
  
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  
  ul.todo li .buttons button:last-of-type:before {
    content: '';
    width: 1px;
    height: 30px;
    background: #edf0f1;
  
    position: absolute;
    top: 10px;
    left: 0;
  }
  
  ul.todo li .buttons button svg {
    width: 22px;
    height: 22px;
  
    position: absolute;
    top: 50%;
    left: 50%;
  
    margin: -11px 0 0 -11px;
  }
  
  ul.todo li .buttons button.complete svg {
    border-radius: 11px;
    border: 1.5px solid #25b99a;
  
    -webkit-transition: background 0.2s ease;
  
    -o-transition: background 0.2s ease;
  
    transition: background 0.2s ease;
  }
  
  ul.todo#completed li .buttons button.complete svg {
    background: #307672;
    border: 0px;
  }
  ul.todo:not(#completed) li .buttons button.complete:hover svg {
    background: rgba(37, 185, 154, 0.75);
  }
  ul.todo:not(#completed) li .buttons button.complete:hover svg .fill {
    fill: #fff;
  }
  ul.todo#completed li .buttons button.complete svg .fill {
    fill: #fff;
  }
  ul.todo li .buttons button.remove:hover svg .fill {
    fill: #e85656;
  }
  
  ul.todo li .buttons button.complete svg .fill {
    fill: #25b99a;
  }
  
  ul.todo#completed {
    position: relative;
    padding: 60px 0 0 0;
    color:#307672
  }
  
  
  
  ul.todo#todo:empty:after {
    content: 'You have nothing to-do!';
    margin: 15px 0 0 0;
  }
  
  ul.todo#completed:empty:after {
    content: 'You have yet to complete any tasks.';
  }
  
  ul.todo#todo:after,
  ul.todo#completed:after {
    width: 100%;
    display: block;
    text-align: center;
    font-size: 12px;
    color: #aaa;
  }
  


